.grid {
    height: 50vh;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-self: center;
}

.stopwatch {
    width: 300px;
    background-color: #fff;
    box-shadow: 0 20px 20px 0 rgba(252, 169, 169, 0.6);
    border-radius: 5px;
    padding: 15px;

    .display {
        font-size: 50px;
        font-family: sans-serif;
        text-align: center;
        margin-top: 10px;

        :not(:last-child)::after {
            content: ':';
        }
    }

    .controls {
        display: flex;

        button {
            flex-grow: 1;
            margin: 0 5px 4px;
            border-radius: 10px;
            box-shadow: 0 4px rgba(0, 0, 0, 0.75);
            border: 0;
            outline: 0;
            font-size: 20px;
            color: white;
            cursor: pointer;
            font-weight: bold;

            &:active {
                margin-bottom: 0;
                margin-top: 4px;
                box-shadow: none;
            }
        }

        .start {
            background-color: #66CDAA;

            &:hover {
                background-color: #3CB371;
            }
        }

        .stop {
            background-color: #F08080;

            &:hover {
                background-color: #CD5C5C;
            }
        }

        .reset {
            background-color: #7B68EE;

            &:hover {
                background-color: #6A5ACD;
            }
        }
    }
}